





























import { Vue, Component, Watch, Emit, Prop } from 'vue-property-decorator';
import { themeModule } from '@/store/modules/theme';

@Component({
  name: 'NavBar',
})
export default class NavBar extends Vue {
  private theme: string = '';
  private backgroundColor: string = '';
  private color: string = '';
  @Prop(Boolean)
  private isShowView!: boolean;

  // 监听选择主题颜色，将选择的颜色保存到本地并计算出字体颜色
  @Watch('backgroundColor', { immediate: true })
  private changeColor(newVal: string, oldVal: string) {
    if (oldVal) {
      themeModule.ResetTheme(newVal);
    }
    themeModule.GetTheme();
    this.theme = themeModule.theme;
    themeModule.ResetColor(this.theme);
    this.color = themeModule.fontColor;
    this.backgroundColor = themeModule.theme;
  }

  private check(type: 'add' | 'download' | 'choose') {
    if (type === 'add' && !this.isShowView) {
      this.addPage();
    } else if (type === 'download' && this.isShowView) {
      this.download();
    } else if (type === 'choose' && !this.isShowView) {
      this.choose();
    }
  }

  @Emit('on-add')
  private addPage() {
    return true;
  }

  @Emit('download')
  private download() {
    return true;
  }

  @Emit('choose')
  private choose() {
    return true;
  }
}
